@import "../common/_vars.scss";
footer{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    margin-top: 3%;
}

.logo_content{
    width: 3%;
    img{
        width: 100%;
        height: 100%;
    }
}

